.wrapper{
  display: flex;
}
  
  .slider {
    /* -webkit-appearance: none; */
    -webkit-appearance: slider-vertical;
    width: 1rem;
    height: 60vh;
    border-radius: 5px;
    background: var(--volumeSlider);
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--volumeSlider);
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--volumeSlider);
    cursor: pointer;
  }
  