.item {
    background-color: var(--playlistBackground);
    color: var(--playlistText);
    text-align: center;
    margin: 5px 0;
    padding: 3px 0;
    border-radius: 5px;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
  }
  
  .active {
    color: var(--playlistTextHoverActive);
    font-family: 'Quicksand', sans-serif;
    font-size: 18px;
  }
  
  .item:hover {
    color: var(--playlistTextHoverActive);
    cursor: pointer;
  }
  