.search {
    font-family: 'Quicksand', sans-serif;
    height: 40px;
    border: none;
    font-size: 18px;
    width: 100%;
    margin: 0 auto 10px auto;
    background-color: var(--searchBackground);
    color: var(--searchText);
    padding-left: 20px;
    border-radius: 10px;
  }
  
  .search::placeholder {
    color: var(--searchPlaceHolder);
  }
  