.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.full{
  display: flex !important;
  height: 100vh;
  min-width: 100vw;
}

.Flex__Main__Top{
  padding-top: 2rem;
  display: flex;
  width: 100vw;
  justify-content: center;
}

.Image__MetaData{
  flex: 0.3;
}
.Image__MetaData1{
  width: 500px ;
  height: 500px ;
}

.Controls__Main{
  flex: 0.1;
  display: flex;
  height: 500px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-left: 3rem;
  padding-right: 3rem;
}
