@import url("https://fonts.googleapis.com/css2?family=Varela+Round&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}

body {
  background-color: #1d1b1b;
  /* background-color: #ffffff; */
}

:root {
  --tagsBackground: #9440f3;
  --tagsText: #ffffff;
  --tagsBackgroundHoverActive: #2cc0a0;
  --tagsTextHoverActive: #ffffff;
  --searchBackground: #1d1b1b;
  --searchText: #ffffff;
  --searchPlaceHolder: #575a77;
  --playerBackground: #1d1b1b;
  --titleColor: #ffffff;
  --timeColor: #ffffff;
  --progressSlider: #9440f3;
  --progressUsed: #ffffff;
  --progressLeft: #20ce66;
  --volumeSlider: #9440f3;
  --volumeUsed: #ffffff;
  --volumeLeft: #1d1b1b;
  --playlistBackground: #1d1b1b;
  --playlistText: #575a77;
  --playlistBackgroundHoverActive: #1d1b1b;
  --playlistTextHoverActive: #ffffff;
}

[data-theme='dark'] {
  --tagsBackground: #9440f3;
  --tagsText: #1d1b1b;
  --tagsBackgroundHoverActive: #2cc0a0;
  --tagsTextHoverActive: #1d1b1b;
  --searchBackground: #ffffff;
  --searchText: #1d1b1b;
  --searchPlaceHolder: #575a77;
  --playerBackground: #ffffff;
  --titleColor: #1d1b1b;
  --timeColor: #1d1b1b;
  --progressSlider: #9440f3;
  --progressUsed: #1d1b1b;
  --progressLeft: #ffffff;
  --volumeSlider: #9440f3;
  --volumeUsed: #1d1b1b;
  --volumeLeft: #ffffff;
  --playlistBackground: #ffffff;
  --playlistText: #575a77;
  --playlistBackgroundHoverActive: #ffffff;
  --playlistTextHoverActive: #1d1b1b;
}
